.RulesList {
  list-style: none;
  padding: 0px;
}

.RulesList-item {
  list-style: none;
  min-height: 3em;
  border-bottom: 1px solid #cecece;
  display: -ms-flexbox;
  display: flex;
}

.RulesList-item:last-child {
  border: 0px;
}

.RulesList-item-title {
  -ms-flex-item-align: center;
      align-self: center;
  -ms-flex: 1 1;
      flex: 1 1;
}

.RulesList-delete {
  background: none;
  border: none;
  font-size: 32px;
  font-weight: lighter;
}

@media (max-width: 400px) {
  .RulesList-item {
    padding: 1em;
  }
}



